.Unit {
    position: absolute;
    user-select: none;
    /* transform-origin: -50% -50%; */
}

.Unit.is-static {
    z-index: 1;
}

.Unit.is-ground-mover {
    z-index: 2;
}

.Unit.is-weapon {
    z-index: 3;
}

.Unit.is-helicopter {
    z-index: 4;
}

.Unit.is-unit {
    z-index: 5;
}

.Unit .path {
    position: absolute;
}

.Unit .path svg {
    fill: none;
    stroke: #999;
    stroke-width: 5;
    stroke-opacity: 0.12;
}

.Unit .primary-container {
    position: absolute;
    width: 32px;
    height: 32px;
    user-select: none;
}

.Unit .graphics-container {
    width: 100%;
    height: 100%;
    user-select: none;
    /* transform-origin: -50% -50%; */
}

.Unit .unit-image {
    height: 100%;
    width: 100%;
    transform: rotate(45deg);
    user-select: none;
}

.Unit .inactive .unit-image {
    opacity: 0.5;
}
.Unit .ghost .unit-image {
    opacity: 0.2;
}

.Unit p {
    font-size: 10px;
}